import React, { Fragment, useEffect, useRef, useState } from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHeart, faHeartPulse, faUser, faCakeCandles } from '@fortawesome/free-solid-svg-icons';
import className from 'classnames/bind';
import style from './Home.module.css';
import { Link, Route, Routes, useNavigate } from 'react-router-dom';
import Love from '../Love';
import LineHeart from '~/component/Layout/component/LineHeart';

const cx = className.bind(style);
let ind = 0;
let yes = 0;
function Home() {
    const navigate = useNavigate();
    const [comment, setComment] = useState(
        'Nay 20/10 chc b anh ngy cng xinh p hc gii, nu n vi t n m tm nha, ung nhiu nc , tha li cho anh na m, bit b anh yu anh nht ri, hong n gin anh u m nh',
    );
    // let arrSr = ['Chng xin li v m!', 'Chng bit li ri ', 'Chng xin li v nhiu lm', 'Yu v nhiu m', 'V'];
    let arrSr = [
        'Chng xin li v m!',
        'Chng bit li ri .',
        'Chng xin li v nhiu lm.',
        'Yu v nhiu m!',
        'Thng v lm, ng gin na nha.',
        'Chng sai ri, tha li cho chng nha.',

        'Chng xin li v yu.',
        'ng gin chng na m, xin li v nhiu.',

        'Chng yu v, xin li v  lm v bun.',
        'Anh xin li, v tha th cho anh nha!',

        'Chng bit sai ri ',
    ];

    const [title, setTitle] = useState('KHNG');
    // To ref  tham chiu n cc counter
    const counterRefs = useRef([]);

    // S dng useEffect  cp nht gi tr ca cc counter sau khi render
    useEffect(() => {
        counterRefs.current.forEach((counter) => {
            counter.innerText = '0';

            const updateCounter = () => {
                const target = +counter.getAttribute('data-target');
                const c = +counter.innerText;

                const increment = target / 300;

                if (c < target) {
                    counter.innerText = `${Math.ceil(c + increment)}`;
                    setTimeout(updateCounter, 100);
                } else {
                    counter.innerText = target;
                }
            };

            updateCounter();
        });
    }, []); // Chy effect sau khi render ln u
    const setComment_ = () => {
        let length = arrSr.length;
        const randomNumber = Math.floor(Math.random() * length);
        if (yes === 1) {
            navigate('/love');
        }
        if (ind === 13) {
            setTitle('Tha li cho chng ');
            setComment('Yu V nhiu nhiu lm');
            yes = 1;
        } else {
            setComment(arrSr[randomNumber]);
            ind++;
        }
    };

    return (
        <div className={cx('wrapper')}>
            <div className={cx('comment')}>{comment}</div>
            <FontAwesomeIcon icon={faHeart} className={cx('floating-heart')} />
            <FontAwesomeIcon icon={faHeart} className={cx('floating-heart')} />
            <FontAwesomeIcon icon={faHeart} className={cx('floating-heart')} />
            <div className={cx('box')}>
                <div className={cx('counter-container')}>
                    <FontAwesomeIcon icon={faHeartPulse} />
                    <div className={cx('counter')} data-target="1008" ref={(el) => (counterRefs.current[0] = el)}></div>
                </div>
                <div className={cx('counter-container')}>
                    <FontAwesomeIcon icon={faHeart} />
                    <div className={cx('counter')} data-target="71" ref={(el) => (counterRefs.current[1] = el)}></div>
                </div>
                <div className={cx('counter-container')}>
                    <FontAwesomeIcon icon={faCakeCandles} />
                    <div className={cx('counter')} data-target="2501" ref={(el) => (counterRefs.current[2] = el)}></div>
                </div>
            </div>
            <div className={cx('action-btn')}>
                <Link className={cx('btn-love')} to="/love">
                    Tm tha
                </Link>
                <div
                    className={cx('btn-custom')}
                    onClick={() => {
                        setComment_();
                    }}
                >
                    <LineHeart title={title} />
                </div>
            </div>

            {/* <div className={cx('LineHeart')}></div> */}
        </div>
    );
}

export default Home;
